import { useDispatch, useSelector } from "react-redux"
import { deleteEvents,createEvents } from "../../reducers/eventReducer"
import { Table } from 'react-bootstrap'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { useState } from "react";
import { notice } from "../../reducers/notificationReducer";
import { editoiTapahtumaa } from "../../reducers/eventReducer";


function Events(){
    const dispatch = useDispatch()
    const [text,setText] = useState("");
    const [editText,setEditText] = useState("");
    const user = useSelector(state => state.currentUser)
const events = useSelector(state => state.events)
const handleDelete = async (id) => {
   // const event = events.find((n) => n.id === id)
    if (window.confirm("are you sure you want to delete?")) {
      dispatch(deleteEvents(id))
      dispatch(notice("poistit tapahtuman",3))
    }
  }

const handleEdit = async (text,id) => {
    const updateTarget = events.find(n => n.id === id)
    const eventObj = {
        text:text,
        id:updateTarget.id
    }
    dispatch(editoiTapahtumaa(eventObj,id))
    dispatch(notice("päivitit tapahtuman",5))
    setEditText("")
}

const addEvent =(event) => {
    event.preventDefault()
const newEvent = ({
    text: text
})
dispatch(createEvents(newEvent))
dispatch(notice(`lisäsit tapahtuman ${text}`,3))
setText("")
}
const handleEditTextChange = (event) => {
    console.log(event.target.value)
    setEditText(event.target.value)
  }
const handleTextChange = (event) => {
    console.log(event.target.value)
    setText(event.target.value)
  }


const event = events.map(x =>{
    return(
        <div key={x.id}>
        <Table striped>
            <tbody >
        <tr >
            <td>
            <input  style={{margin:"10px"}} type="text"  placeholder={x.text} onChange={handleEditTextChange}></input>
            <Button type="button" style={{margin:"10px"}} onClick={() => handleEdit(editText,x.id)}>Muokkaa</Button>
            <Button type="button" onClick={() => handleDelete(x.id)}>Poistha</Button>
            </td>
        </tr>
        </tbody>
        </Table>
        </div>
    )
})
if(user){
    return(
        <div className="ajanvaraus" style={{height:"100vh"}}>
            {event}
            <Form onSubmit={addEvent}>
            <Form.Group>
            <Form.Control id="text"
            value={text}
            onChange={handleTextChange}
            type="text"
            placeholder="tekstiä"></Form.Control>
            <Button id="addBlog" type="submit">
            lisää
          </Button> 
          </Form.Group>
            </Form>
        </div>

    )}
else
{
    return(
    <>
    <h2 style={{color:"white"}}>Kirjaudu sisään ensin</h2>
    </>
    )
}
}
export default Events